class Check {}
Object.assign(Check.prototype, {
  Name(value) {
    if (!value) {
      return '用户名不能为空'
    }
    if (!new RegExp('^[a-zA-Z0-9_\u4e00-\u9fa5\\s·]+$').test(value)) {
      return '用户名不能有特殊字符'
    }
    if (/(^_)|(__)|(_+$)/.test(value)) {
      return "用户名首尾不能出现下划线\t'_'"
    }
    if (/^\d+$/.test(value)) {
      return '用户名不能全为数字'
    }
  },
  Mobile(value) {
    if (!value) {
      return '手机号不能为空'
    }
    if (!/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/.test(value)) {
      return '手机号格式不正确'
    }
  },
  Email(value) {
    if (!value) {
      return '邮箱不能为空'
    }
    if (!/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(value)) {
      return '邮箱格式不正确'
    }
  },
  Password(value) {
    if (!value) {
      return '密码不能为空'
    }
    if (value.length > 12 || value.length < 6) {
      return '密码长度应为6~12位'
    }
  }
})
export default Check
