<template>
  <div>
    <bread></bread>
    <el-card class="box-card" :body-style="{ padding: '20px' }">
      <el-input
        placeholder="请输入内容"
        v-model="paramsObj.query"
        class="input-with-select"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="search"
        ></el-button>
      </el-input>
      <el-button type="primary" @click="dialogVisibleAdd = true"
        >添加用户</el-button
      >
      <!-- 插入表格 -->
      <el-table :data="usersData" border style="width: 100%">
        <el-table-column type="index" label="#" width="50px"></el-table-column
        ><el-table-column prop="username" label="姓名"> </el-table-column
        ><el-table-column prop="email" label="邮箱"> </el-table-column
        ><el-table-column prop="mobile" label="电话"> </el-table-column
        ><el-table-column prop="role_name" label="角色"> </el-table-column
        ><el-table-column prop="mg_state" label="状态">
          <template slot-scope="scoped">
            <!-- slot-scope="scope" 来取得作用域插槽:data绑定的数据，scope可以随便替换其他名称，只是定义对象来代表取得的data数据，便于使用-->
            <el-switch
              v-model="scoped.row.mg_state"
              @change="change(scoped.row)"
            >
            </el-switch> </template></el-table-column
        ><el-table-column label="操作"
          ><template slot-scope="scoped">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="edit(scoped.row)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="del(scoped.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="assignRoles(scoped.row.id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 设置分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="paramsObj.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!-- 添加用户 dialog 对话框 -->
      <el-dialog
        title="添加用户"
        :visible.sync="dialogVisibleAdd"
        width="50%"
        :before-close="handleCloseAdd"
      >
        <el-form
          :model="formAdd"
          ref="addForm"
          :rules="rules"
          label-width="80px"
          :show-message="true"
        >
          <el-form-item label="用户名" prop="username">
            <el-input v-model="formAdd.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input
              type="password"
              v-model="formAdd.password"
            ></el-input> </el-form-item
          ><el-form-item label="邮箱" prop="email">
            <el-input v-model="formAdd.email"></el-input> </el-form-item
          ><el-form-item label="手机" prop="mobile">
            <el-input v-model="formAdd.mobile" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleAdd = false">取 消</el-button>
          <el-button type="primary" @click="addEnter('addForm')"
            >确 定</el-button
          >
        </span>
      </el-dialog>

      <!-- 编辑用户 dialog 对话框 -->
      <el-dialog
        title="编辑用户"
        :visible.sync="dialogVisibleEdit"
        width="50%"
        :before-close="handleCloseEdit"
      >
        <el-form
          :model="formEdit"
          ref="editForm"
          :rules="rules"
          label-width="80px"
          :show-message="true"
        >
          <el-form-item label="用户名">
            <el-input :value="username" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="formEdit.email"></el-input> </el-form-item
          ><el-form-item label="手机" prop="mobile">
            <el-input v-model="formEdit.mobile" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleEdit = false">取 消</el-button>
          <el-button type="primary" @click="editEnter('editForm')"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import Check from '../../check.js'
const check = new Check()
export default {
  data () {
    // 用户名输入框表单验证
    var checkUsername = (rule, value, callback) => {
      const msg = check.Name(value)
      callback(msg && new Error(msg))
    }
    // 用户密码输入框表单验证
    var checkPsd = (rule, value, callback) => {
      const msg = check.Password(value)
      callback(msg && new Error(msg))
    }
    // 邮箱输入格式验证
    var checkEmail = (rule, value, callback) => {
      const msg = check.Email(value)
      callback(msg && new Error(msg))
    }
    // 手机输入格式验证
    var checkMobile = (rule, value, callback) => {
      const msg = check.Mobile(value)
      callback(msg && new Error(msg))
    }
    return {
      dialogVisibleAdd: false, // 控制添加对话框按钮
      dialogVisibleEdit: false, // 控制编辑对话框按钮
      total: 0,
      current_page: 1,
      paramsObj: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      formAdd: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      formEdit: {
        email: '',
        mobile: ''
      },
      username: '', // 编辑对话框用户名
      id: '', // 编辑对话框id
      usersData: [],
      rules: {
        // 规则栏
        password: [{ validator: checkPsd, trigger: 'blur' }],
        username: [{ validator: checkUsername, trigger: 'blur' }],
        email: [{ validator: checkEmail, trigger: 'blur' }],
        mobile: [{ validator: checkMobile, trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 获取用户列表
    async getUserData () {
      const res = await this.$axios.get('/users', { params: this.paramsObj })
      if (res.data.meta.status === 200) {
        this.usersData = res.data.data.users
        this.total = res.data.data.total
      } else {
        this.$message = { type: 'error', message: res.data.meta.msg }
      }
    },
    // 按条件搜索
    search () {
      this.getUserData()
    },
    // 分页函数
    handleSizeChange (val) {
      this.paramsObj.pagesize = val
      this.getUserData()
    },
    handleCurrentChange (val) {
      this.paramsObj.pagenum = val
      this.getUserData()
    },
    // 添加用户
    handleCloseAdd (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
      this.$refs.addForm.resetFields()
    },
    addEnter (addForm) {
      this.$refs[addForm].validate(async valid => {
        if (valid) {
          // 表单验证成功
          const res = await this.$axios.post('/users', this.formAdd)
          if (res.data.meta.status === 201) {
            this.getUserData()
            this.$message({ message: '添加成功', type: 'success' })
          } else {
            this.$message({ message: '添加失败', type: 'error' })
          }
        } else {
          this.$message({ message: '输入内容不合规则', type: 'error' })
        }
        this.dialogVisibleAdd = false
      })
    },
    // 修改用户状态
    async change (obj) {
      // 需要添加防抖
      const res = await this.$axios.put(
        `/users/${obj.id}/state/${obj.mg_state}`
      )
      if (res.data.meta.status === 200) {
        this.$message({ message: '修改用户状态成功', type: 'success' })
      } else {
        this.$message({ message: '修改用户状态失败', type: 'error' })
      }
    },
    // 编辑用户
    handleCloseEdit (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    edit (obj) {
      this.formEdit.email = obj.email
      this.formEdit.mobile = obj.mobile
      this.username = obj.username
      this.id = obj.id
      this.dialogVisibleEdit = true
    },
    editEnter (addForm) {
      this.$refs.editForm.validate(async valid => {
        if (valid) {
          // 表单验证成功
          const res = await this.$axios.put('/users/' + this.id, this.formEdit)
          console.log(res)
          if (res.data.meta.status === 200) {
            this.$message({ message: '修改成功', type: 'success' })
          } else {
            this.$message({ message: '修改失败', type: 'error' })
          }
        } else {
          this.$message({ message: '输入内容不合规则', type: 'error' })
        }
        this.dialogVisibleEdit = false
        this.getUserData()
      })
    },
    // 删除用户
    del (id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        cancelButtonText: '取消',
        confirmButtonText: '确定',
        type: 'warning',
        center: true
      })
        .then(async () => {
          const res = await this.$axios.delete('/users/' + id)
          if (res.data.meta.status === 200) {
            this.getUserData()
            this.$message({ type: 'success', message: '删除成功!' })
          } else {
            this.$message({ type: 'error', message: '删除失败!' })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 分配角色权限
    assignRoles (id) {}
  },
  created () {
    // 在实例创建完成后被立即调用
    this.getUserData()
  }
}
</script>

<style lang="less" scoped>
.el-input-group {
  width: 30% !important;
  margin-right: 20px;
}
.el-table {
  margin-top: 15px;
  /deep/.cell {
    font-size: 12px;
  }
}
</style>
